.map {
    display: flex;

    &__remove-user, &__add-user{
        font-size: 14px;
        width: 21px;
        height: 21px;
        line-height: 21px;
        font-weight: 600;
        background-color: #a5a5a5;
        border: none;
        outline: none;
        border-radius: 10px;
    }

    &-top {
        width: 220px;
        display: flex;
        flex-direction: column;
    }

    &__remove-user {

        margin-left: 3px;
    }

    &__input {
        font-family: 'Montserrat', Arial, sans-serif;
        font-size: 16px;
        width: 150px;
        height: 18px;
        line-height: 18px;
        margin-right: 5px;
        margin-bottom: 5px;
        border: 1px solid #a5a5a5;
        
    }

    &__halndle-user-list {
        display: flex;
        flex-direction: column;
        width: 100px;
    }

    &__halndle-user-item {
        padding: 2px;
        margin-right: 5px;
        margin-bottom: 3px;
        border-bottom: 1px solid #a5a5a5;

        &:first-child {
            border-bottom: none;
        }

        &__error {
            color: red;
        }
    }
}